@import 'timing';

$rotations: 0 45 90 135 180 225 270 315;

@each $rotation in $rotations {
    @keyframes rotate-#{$rotation} {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(#{$rotation}deg)
        }
    }

    .rotate--animate.rotate-#{$rotation} {
        animation: rotate-#{$rotation} $timing-medium both;
    }

    .rotate-#{$rotation}:not(.rotate--animate) {
        transform: rotate(#{$rotation}deg)
    }
}
