.html {
    line-height: 1.4em;

    // global overrides
    h1, h2, h3, h4, h5, h6, p, a,strong, sub, sup, b, u, i, ol, ul, li {
        line-height: 1.4em;
    }

    /* Headings */
    h1, h2, h3, h4, h5, h6 {
        font-weight: bold;
    }

    a,
    a:visited {
        color: var(--html-link-color);
    }

    a:hover {
        text-decoration: none;
    }

    p {
        padding: 4px 0 4px;
    }

    ul,ol {
        margin: 8px 8px;
    }
  
    ul {
        list-style-type: disc;
    }

    ol {
        list-style-type: decimal;
    }  
    
    li {
        margin: 0 0 0 16px;
        padding-left: 4px;
        padding-bottom: 1em;
    }

    b, strong {
        font-weight: bold;
    }

    em, i {
        font-style: italic;
    }

    sub {
        vertical-align: sub;
        font-size: 0.8em;
    }

    sup {
        vertical-align: super;
        font-size: 0.8em;
    }

    mark,
    dt {
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        overflow: hidden;
    }

    dt {
        float: left;
        clear: left;
        margin: 0 4px 0 0;
    }

    dd:after {
        content: "";
        display: block;
        margin-bottom: 12px;
    }
}