@import 'colors';

/*
 * Text utility classes
 */
@mixin textSize($fontSize) {
    font-size: $fontSize + px;
    letter-spacing: (0.025 * $fontSize) + px;
    line-height: 1.4em;
}

@mixin textUppercase($fontSize) {
    font-size: $fontSize + px;
    letter-spacing: (0.06 * $fontSize) + px;
    line-height: 1.25em;
    text-transform: uppercase;
}

@mixin textWrap() {
    /* cross browser word wrapping */
    word-wrap: break-word;
    overflow-wrap: break-word;
}

@each $textSize in 8 9 10 11 12 13 14 16 18 20 22 24 {
    .text-#{$textSize} {
        @include textSize($textSize);
    }

    .text-#{$textSize}.text-uppercase {
        @include textUppercase($textSize);
    }
}

.text {
    &-header {
        font-weight: 700;
    }

    &--code {
        font-family: 'Courier New', Courier, monospace;
    }

    &-uppercase {
        text-transform: uppercase;
    }

    &-xlight {
        font-weight: 200;
    }

    &-light {
        font-weight: 300;
    }

    &-medium {
        font-weight: 500;
    }

    &-semibold {
        font-weight: 600;
    }

    &-bold {
        font-weight: 700;
    }
    
    &-underline {
        text-decoration: underline;
    }

    &-italic {
        font-style: italic;
    }
    
    &-bright {
        color: $gray-100;
    }

    &-dim {
        color: $gray-60;
    }

    &-error {
        color: $error;
    }

    &-left {
        text-align: left;
    }

    &-center {
        text-align: center;
    }

    &-right {
        text-align: right;
    }

    &-ellipsis {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: block;
    }

    &-link {
        color: inherit;
        text-decoration: none;
    }

    &-success {
        color: $green;
    }

    &-danger {
        color: $red;
    }

    &-info {
        color: $blue;
    }

    &-nowrap {
        white-space: nowrap;
    }
}
