@import 'colors';
@import 'utility/shadows';

:root {
  // ---------- Core colors, reused across components -----------
  --blue: #{$blue-60};
  --light-blue: #{$blue-10};
  --red: #DE3838;
  --pink: #dc2d72;
  
  // States
  --selected: var(--blue);
  --selected-background: var(--light-blue);
  --active-background: #{$gray-75};
  --hover-background: var(--light-blue);
  --focus-background: #{$blue-20};
  --accent: var(--blue);

  // buttons
  --button-background--primary: #{$red};
  --button-background--flat: #{$gray-80};
  --button-background--hover: var(--hover-background);
  --button-background--active: var(--active-background);
  --button-background--selected: var(--selected-background);
  --button-background--outline: transparent;

  // --button-color--flat: #{$gray-20};
  --button-color--flat: #{$gray-40};
  --button-color--selected: var(--selected);

  // ---------- Core animation timing ----------
  --animation-hover-speed: 100ms;


  // ---------- View theme common values ----------
  --view-common-background: #{$gray-100};
  --view-common-background--hover: #{$gray-90};
  --view-primary-color: #{$gray-10};
  --view-primary-color--hover: #{$gray-20};
  --view-secondary-color: #{$gray-40};
  --view-minor-color: #{$gray-60};
  --view-common-border: 1px solid #{$gray-80};
  --view-common-shadow: #{$common-shadow};

  // ---------- Component colors -----------
  
  // View Page
  --view-page-bg: var(--view-common-background);
  // --view-page-border: var(--view-common-border);
  --view-page-border: none;

  // View Column
  --view-column-background: var(--view-common-background);
  --view-column-color: var(--view-primary-color);
  --view-column-border: transparent;

  --view-column-toggle-background: rgba(255,255,255,0.8);
  --view-column-toggle-background--hover: var(--button-background--hover);
  --view-column-toggle-color: var(--view-column-color);
  --view-column-toggle-color--hover: var(--view-column-color);
  --view-column-toggle-shadow: var(--view-common-shadow);
  

  // View Drawer
  $view-drawer-border: rgba($gray-80, 0.8);
  
  --view-drawer-background--hover: var(--button-background--hover);
  --view-drawer-background--closed: var(--view-common-background);
  --view-drawer-background--active: var(--view-common-background);
  
  --view-drawer-color: var(--view-primary-color);
  --view-drawer-color--hover: var(--view-primary-color--hover);
  --view-drawer-shadow: 0 -1px 0 0 rgba(28,42,63,0.16);

  --view-drawer-toggle-color: #{$gray-70};
  --view-drawer-toggle-color--hover: var(--view-primary-color);
  --view-drawer-toggle-background--active: var(--selected);
  --view-drawer-toggle-color--active: white;

  // View Title (view-title only experience)
  --view-title-color: var(--view-primary-color);
  --view-title-background: rgba(255,255,255,0.8);
  --view-title-shadow: var(--view-common-shadow);

  // View Loader
  --view-loader-scrim-bg: rgba(0, 0, 0, 0.1);
  --view-loader-scrim-bg--hover: rgba(0, 0, 0, 0.2);
  $view-loader-button-bg: rgba($gray-0, 0.8);
  --view-loader-button-bg: #{$view-loader-button-bg};
  --view-loader-button-bg--hover: rgba(255, 255, 255, 0.8);
  $view-loader-icon-color: rgba($gray-100, 0.9);
  --view-loader-icon-color: #{$view-loader-icon-color};
  --view-loader-icon-color--hover: var(--accent);
  --view-loader-label-color: #{$gray-0};
  --view-loader-label-color--hover: #{$gray-100};
  --view-loader-progress-color: #{$gray-100};
  --view-loader-progress-color--hover: var(--accent);


  // View Load Progress
  $view-progress-bg: rgba($gray-0, 0.8);
  $view-progress-secondary-color: rgba($gray-100, 0.8);
  --view-progress-bg: #{$view-progress-bg};
  --view-progress-primary-color: #{$gray-100};
  --view-progress-secondary-color: #{$view-progress-secondary-color};

  // button loader
  --view-loader-button-color: var(--blue);

  // View Menu
  $viewMenuButton: var(--accent);
  
  --view-menu-button-bg: #{$viewMenuButton};
  --view-menu-button-bg--hover: #{$viewMenuButton};
  --view-menu-button-bg--opened: #{$viewMenuButton};
  --view-menu-button-bg--selected: #{$viewMenuButton};
  --view-menu-button-box-shadow--opened: 0 0 1px 1px #{$viewMenuButton};
  --view-menu-button-color: white;
  --view-menu-button-color--hover: white;
  --view-menu-button-color--selected: white;
  --view-menu-button-color--opened: white;

  --view-menu-bg: var(--view-common-background);
  --view-menu-color: var(--view-primary-color);
  --view-menu-shadow: var(--view-common-shadow);
  --view-menu-divider-bg: #{$gray-80};
  

  // View Pagination
  --view-pagination-bar-bg: var(--view-common-background);
  --view-pagination-bar-border: var(--view-common-border);
  --view-pagination-bar-button-color: #{$gray-60};
  $view-pagination-bar-button-color--disabled: rgba($gray-60, 0.2);
  --view-pagination-bar-button-color--disabled: #{$view-pagination-bar-button-color--disabled};
  --view-pagination-bar-button-color--hover: #{$gray-20};

  // View Tools
  --view-tools-border: 1px solid rgba(0, 0, 0, 0.1);

  // View Help
  $view-help-bg: rgba($gray-0, 0.9);
  --view-help-bg: #{$view-help-bg};
  --view-help-color: white;
  --view-help-border: 1px solid rgba(255,255,255,0.3);
  --view-help-tab-border: 3px solid var(--selected);

  // View Description
  --view-description-primary-color: var(--view-primary-color);
  --view-description-secondary-color: var(--view-secondary-color);
  --view-description-link-color: var(--blue);
  --view-description-border: var(--view-common-border);

  // View Panel
  --view-panel-border: var(--view-common-border);
  --view-panel-color: var(--view-secondary-color);

  // View Chapters
  --view-chapters-bg: transparent;
  --view-chapters-bg--hover: var(--hover-background);
  --view-chapters-bg--selected: var(--selected-background);
  --view-chapters-color: var(--view-primary-color);
  --view-chapters-color--hover: var(--view-primary-color);
  --view-chapters-color--selected: var(--view-primary-color);
  --view-chapters-border: 4px solid transparent;
  --view-chapters-border--selected: 4px solid var(--selected);

  --view-chapters-thumb-bg: #{$gray-90};
  
  // View Labels
  --view-labels-bg: transparent;
  --view-labels-bg--selected: var(--selected-background);
  --view-labels-bg--hover: var(--hover-background);

  --view-labels-border: 4px solid transparent;
  --view-labels-border--hover: 4px solid var(--selected);
  --view-labels-border--selected: 4px solid var(--selected);

  --view-labels-pin-border: 1px solid var(--view-primary-color);
  --view-labels-pin-bg: transparent;
  --view-labels-pin-color: var(--view-primary-color);

  --view-labels-description-color: var(--view-secondary-color);

  // View Internal Header
  --view-internal-header-border: var(--view-common-border);
  --view-internal-header-breadcrumb-color: var(--view-secondary-color);

  // View Search
  --view-search-tab-color: #{$gray-40};
  --view-search-tab-active-color: #{$gray-10};
  --view-search-tab-hover-color: #{$gray-10};
  --view-search-tab-active-border: #{$blue-60};
  --view-search-tab-hover-border: #{$gray-70};
  --view-search-input-bg: var(--view-common-background);
  --view-search-active-color: var(--view-primary-color);
  --view-search-inactive-color: var(--view-minor-color);
  --view-search-exit-color: #{$gray-40};
  --view-search-exit-bg: #{$gray-70};
  --view-search-divider-color: var(--view-minor-color);
  --view-search-border-color: rgba(0, 0, 0, 0.1);

  // Pin Button
  --pin-button-pin-background: var(--button-background--flat);
  --pin-button-pin-background--selected: var(--blue);
  --pin-button-pin-background--hover: var(--button-background--hover);
  --pin-button-pin-background--active: var(--button-background--active);
  --pin-button-pin-background--primary: var(--button-background--primary);
  --pin-button-pin-background--floating: white;
  --pin-button-pin-background--dark: #{$gray-20};
  --pin-button-pin-border: 1px solid #{$gray-40};
  --pin-button-pin-color: var(--button-color--flat);
  --pin-button-pin-color--selected: white;
  --pin-button-pin-color--primary: white;
  --pin-button-pin-color--dark: #{$gray-90};
  --pin-button-label-color: var(--button-color--flat);
  --pin-button-label-color--selected: var(--button-color--selected);
  --pin-button-shadow: var(--view-common-shadow);

  // Scene Button
  // --scene-button-bg: white;
  --scene-button-bg: rgba(255,255,255,0.8);
  --scene-button-bg--hover: #{$gray-90};
  --scene-button-bg--selected: var(--selected-background);
  
  --scene-button-color: #{$gray-20};
  --scene-button-color--hover: #{$gray-20};
  --scene-button-color--selected: white;

  --scene-button-shadow: var(--view-common-shadow);

  // Scrubber
  $scrubber-track-bg: rgba($gray-20, 0.2);
  --scrubber-track-bg: #{$scrubber-track-bg};
  --scrubber-track-color: #{$gray-20};
  --scrubber-handle-color: #{$gray-40};
  --scrubber-handle-color--hover: var(--blue);
  --scrubber-handle-shadow: var(--view-common-shadow);

  // Tree node
  --tree-node-bg--hover: #{$gray-90};
  --tree-node-bg--selected: var(--selected-background);
  --tree-node-border: 4px solid transparent;
  --tree-node-border--selected: 4px solid var(--selected);
  --tree-node-color: var(--view-secondary-color);
  --tree-node-color--selected: var(--view-primary-color);
  --tree-node-color--hidden: var(--view-minor-color);
  --tree-node-color--hover: var(--view-primary-color);
  --tree-node-collapse-color: #{$gray-80};
  --tree-node-collapse-color--hover: #{$gray-70};
  --tree-node-button-bg: transparent;
  --tree-node-button-color: var(--view-secondary-color);
  --tree-node-button-bg--hover: transparent;
  --tree-node-button-color--hover: inherit;

  // Pillbox
  --pillbox-bg: rgba(255,255,255,0.8);
  --pillbox-bg--hover: var(--view-common-background--hover);
  --pillbox-color: var(--view-primary-color);
  --pillbox-color--hover: var(--view-primary-color--hover);
  --pillbox-shadow: var(--view-common-shadow);
  --pillbox-border: 1px solid rgba(0, 0, 0, 0.1);

  // Widget Shell
  --widget-shell-border: var(--view-common-border);

  --app-button-color: var(--view-secondary-color);
  --app-button-color--hover: var(--view-primary-color--hover);
  --app-button-bg--hover: #{$gray-70};

  // Formatted HTML
  --html-link-color: var(--blue);
}
