.flex {
    display: flex;
}

.flex-grow {
    flex-grow: 1;
}

.flex-shrink {
    flex-shrink: 1;
}

.flex-25 {
    flex-basis: 25%;
}

.flex-50 {
    flex-basis: 50%;
}

.flex-75 {
    flex-basis: 75%;
}

.flex-100 {
    flex-basis: 100%;
}

.flex-column {
    flex-direction: column;
}

.flex-row {
    flex-direction: row;
}

.items-start {
    align-items: flex-start;
}

.items-center {
    align-items: center;
}

.items-end {
    align-items: flex-end;
}

.justify-start {
    justify-content: flex-start;
}

.justify-center	{
    justify-content: center;
}

.justify-end {
    justify-content: flex-end;
}

.justify-between {
    justify-content: space-between;
}

.justify-around	{
    justify-content: space-around;
}