  /* --------------------------------------------------------------------------
  Single variable font.
  
  Note that you may want to do something like this to make sure you're serving
  constant fonts to older browsers:
  html {
    font-family: 'Inter UI', sans-serif;
  }
  @supports (font-variation-settings: normal) {
    html {
      font-family: 'Inter UI var', sans-serif;
    }
  }
  
  BUGS:
  - Safari 12.0 will default to italic instead of regular when font-weight
    is provided in a @font-face declaration.
    Workaround: Use 'Inter UI var alt' for Safari, or explicitly set
    `font-variation-settings:'slnt' DEGREE`.
  */
  @font-face {
    font-family: 'Inter UI var';
    font-weight: 100 900;
    font-style: oblique 0deg 10deg;
    src: url('#{$inter-font-path}/Inter-UI.var.woff2') format('woff2-variations'),
         url('#{$inter-font-path}/Inter-UI.var.woff2') format('woff2');
  }
  
  /* --------------------------------------------------------------------------
  
  'Inter UI var alt' is recommended for Safari and Edge, for reliable italics.
  
  @supports (font-variation-settings: normal) {
    html {
      font-family: 'Inter UI var alt', sans-serif;
    }
  }
  
  */
  @font-face {
    font-family: 'Inter UI var alt';
    font-weight: 100 900;
    font-style: normal;
    font-named-instance: 'Regular';
    src: url('#{$inter-font-path}/Inter-UI-upright.var.woff2') format('woff2 supports variations(gvar)'),
         url('#{$inter-font-path}/Inter-UI-upright.var.woff2') format('woff2-variations'),
         url('#{$inter-font-path}/Inter-UI-upright.var.woff2') format('woff2');
  }
  @font-face {
    font-family: 'Inter UI var alt';
    font-weight: 100 900;
    font-style: italic;
    font-named-instance: 'Italic';
    src: url('#{$inter-font-path}/Inter-UI-italic.var.woff2') format('woff2 supports variations(gvar)'),
         url('#{$inter-font-path}/Inter-UI-italic.var.woff2') format('woff2-variations'),
         url('#{$inter-font-path}/Inter-UI-italic.var.woff2') format('woff2');
  }
  