/*
 * Generates a set of spacing utility classes
 * e.g. mt-3, pb-4
 */
 $attributes: (margin: m, padding: p);
 $directions: (top: t, right: r, bottom: b, left: l);
 
 $count: 10;
 $spacing: 4px;
 
 @for $i from 1 through $count {
     @each $attribute, $attributeAbbreviation in $attributes {
         .#{$attributeAbbreviation}-#{$i} {
             #{$attribute}: $i * $spacing !important;
         }
 
         .#{$attributeAbbreviation}v-#{$i} {
             #{$attribute}-top: $i * $spacing !important;
             #{$attribute}-bottom: $i * $spacing !important;
         }
 
         .#{$attributeAbbreviation}h-#{$i} {
             #{$attribute}-left: $i * $spacing !important;
             #{$attribute}-right: $i * $spacing !important;
         }
 
         @each $direction, $directionAbbreviation in $directions {
             .#{$attributeAbbreviation}#{$directionAbbreviation}-#{$i} {
                 #{$attribute}-#{$direction}: $i * $spacing !important;
             }
         }
    }
}