@media (max-width: 768px) {
    .hide-sm {
        display: none !important;
    }
}

@media (min-width: 769px) {
    .show-sm {
        display: none !important;
    }
}

@media (max-width: 960px) {
    .hide-md {
        display: none !important;
    }
}

@media (min-width: 993px) {
    .show-md {
        display: none !important;
    }
}
