  --view-loader-button-bg--hover: rgba(255, 255, 255, 0.8);
  --view-loader-icon-color: rgba(255, 255, 255, 0.9);
  --view-loader-icon-color--hover: var(--accent);
  --view-loader-label-color: #101823;
  --view-loader-label-color--hover: #ffffff;
  --view-loader-progress-color: #ffffff;
  --view-loader-progress-color--hover: var(--accent);
