/*
 * # Global box-shadow mixing
 * All components should use the same shadow function to ensure a consistent single light source across the site.
 */
 @mixin box-shadow($size: 1) {
    box-shadow: 0 0 (1px * $size) 0 rgba(50,65,72,0.1);
}

@mixin box-shadow-hover($size: 1) {
    box-shadow: 0 0 (1px * $size) 0 rgba(88,62,176,0.6);
}

@mixin box-shadow-vertical($size: 1) {
    box-shadow: (1px * $size) (1px * $size) (1px * $size) 0 rgba(50,65,72,0.1);
}

$common-shadow: 1px -1px 2px 0 rgba(28,42,63,0.1), 0 1px 2px 0 rgba(28,42,63,0.3);