@import "colors";

/**
 * TEXT INPUTS
 */
[type=text].input,
[type=number].input,
[type=email].input,
[type=password].input,
select.input,
textarea.input {
    border: 1px solid $gray-15;
    border-radius: 4px;
    background-color: $gray-0;
    color: $gray-100;
    display: block;
    min-height: 36px;
    width: 100%;
    font-size: 12px;
    padding: 8px 12px;
    font-family: 'Inter UI', sans-serif;

    @supports (font-variation-settings: normal) {
        font-family: 'Inter UI var alt', sans-serif;
    }
    
    &:focus {
        border: 1px solid $blue-60;
        box-shadow: 0 0 3px $blue-70;
        outline: 0;
    }

    &.ng-invalid:focus {
        border-color: $error;
        box-shadow: 0 0 3px $error;
    }

    &--small {
        font-size: 12px;
        padding: 8px;
        min-height: auto;
    }

    &--large {
        font-size: 16px;
        padding: 12px;
    }

    &:disabled {
        opacity: 0.4;
    }

    &.ng-dirty {
        &.ng-invalid {
            border: 1px solid $error;
        }

        // &.ng-valid { }
    }
}

textarea.input {
    &--nonresizable {
        resize: none;
    }
}

select.input {
    height: 36px;
}

input.theme-dark,
select.theme-dark,
textarea.theme-dark,
.theme-dark .input[type=text],
.theme-dark .input[type=number],
.theme-dark .input[type=email],
.theme-dark .input[type=password],
.theme-dark select.input,
.theme-dark textarea.input {
    background-color: $gray-0;
    border: 1px solid $gray-40;
    color: $gray-100;
}

input.theme-light,
select.theme-light,
textarea.theme-light,
.theme-light .input[type=text],
.theme-light .input[type=number],
.theme-light .input[type=email],
.theme-light .input[type=password],
.theme-light select.input,
.theme-light textarea.input {
    background-color: $gray-100;
    border: 1px solid $gray-60;
    color: $gray-30;
    
    &:focus {
        background-color: $gray-100;
    }
    
    &::placeholder { 
        color: rgba($gray-40, 0.5);
    }
}
