@import "colors";

// material css varibales: default "dark" theme
:root {
    --mat-toggle-border-color: #{$gray-30};
    --mat-toggle-background-color: #{$gray-10};
    --mat-toggle-selected-background-color: #{$gray-40};
    --mat-toggle-selected-color: #{$gray-90};
    --mat-toggle-color: #{$gray-50};
}

// "light" theme
.theme-light {
    --mat-toggle-border-color: #{$gray-30};
    --mat-toggle-background-color: #{$gray-90};
    --mat-toggle-selected-background-color: #{$gray-60};
    --mat-toggle-selected-color: #{$gray-10};
    --mat-toggle-color: #{$gray-50};
}
  
body {
    // Button
    .mat-button-wrapper {
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 12px;
        font-weight: bold;
    }
    
    .mat-button {
        min-width: 32px;
    }
    

    //Diaglog
    .mat-dialog-container {
        color: $gray-20;
    }
    .mat-dialog-actions {
        margin-bottom: -8px;
    }

    .transparent.cdk-overlay-backdrop {
        opacity: 0;
    }
    
    // Checkbox and Radio
    .mat-checkbox .mat-checkbox-ripple,
    .mat-radio-button .mat-radio-ripple {
        height: 32px;
        width: 32px;
        left: calc(50% - 16px);
        top: calc(50% - 16px);
    }

    // Checkbox
    .mat-checkbox-inner-container {
        border-radius: 3px;
    }

    .mat-checkbox-frame {
        border-width: 1px;
        border-radius: 3px;
    }

    .mat-checkbox-indeterminate.mat-accent .mat-checkbox-background, .mat-checkbox-checked.mat-accent .mat-checkbox-background {
        background: $blue-60;
        border-color: $blue-60;
    }

    // Radio
    .mat-radio-container {
        width: 16px;
        height: 16px;
    }

    .mat-radio-outer-circle {
        border-width: 1px;
        width: 16px;
        height: 16px;
    }

    .mat-radio-inner-circle {
        width: 16px;
        height: 16px;
    }

    .theme-dark {
        .mat-checkbox-inner-container {
            background: $gray-0;
        }

        .mat-checkbox-frame {
            border-color: $gray-40;
        }

        .mat-radio-outer-circle {
            border-color: $gray-40;
        }
    }

    .theme-light {
        .mat-checkbox-frame {
            border-color: $gray-60;
        }

        .mat-radio-outer-circle {
            border-color: $gray-60;
        }

        .mat-checkbox-label {
            color: $gray-30;
        }
    }

    .theme-ghost {
        .mat-checkbox-inner-container {
            background: transparent;
        }

        .mat-checkbox-frame {
            border-color: $gray-70;
        }

        .mat-checkbox:hover .mat-checkbox-frame {
            border-color: $gray-100;
        }
    }

    // Divider
    .mat-divider {
        border-top-color: $gray-80;
    }

    // Snackbar
    .mat-snack-bar-container {
        color: $gray-90;
        background: $gray-30;
        border: 1px solid $gray-40;
    }

    .mat-simple-snackbar {
        font-size: 12px;
    }

    .mat-simple-snackbar-action {
        color: $gray-60;

        .mat-button-wrapper {
            font-weight: 400;
        }

        .mat-button-focus-overlay {
            background: $gray-40;
        }

        .mat-button:hover .mat-button-focus-overlay, .mat-stroked-button:hover .mat-button-focus-overlay {
            opacity: .4;
        }
    }

    // Dialog
    .mat-dialog-title {
        margin: 0 0 12px;
    }
    .mat-dialog-actions {
        justify-content: flex-end;

        > * {
            margin-left: 8px;
        }
    }

    // Forms
    .mat-error {
        font-size: 12px;
        color: $error;
    }

    // Menu Item 
    .mat-menu-item:hover:not([disabled]), .mat-menu-item.cdk-program-focused:not([disabled]), 
    .mat-menu-item.cdk-keyboard-focused:not([disabled]), .mat-menu-item-highlighted:not([disabled]) {
        background: $gray-80;

        &--red {
            &:hover {
                background: $primary;
            }
        }
    }

    // Menu Item
    button.mat-menu-item {
        font-size: 12px;
        height: 42px;
        line-height: 44px;
    }


    // Button Toggle
    .mat-button-toggle-group-appearance-standard {
        border: none;
        
        &.mat-button-toggle-group {
            display: inline-flex;    
            
            .mat-button-toggle {
                background-color: var(--mat-toggle-background-color);  
                border: 1px solid var(--mat-toggle-border-color);
                border-right-width: 0;
                color: var(--mat-toggle-color);
                font-size: 12px;
                
                padding: 0px 8px 0px;

                &-label-content {
                    line-height: 36px;
                    padding: 0 12px;
                }

                &:first-child {
                    border-top-left-radius: 8px;
                    border-bottom-left-radius: 8px;
                }
    
                &:last-child {
                    border-top-right-radius: 8px;
                    border-bottom-right-radius: 8px;

                    border-right-width: 1px;
                }
    
                &-checked {
                    background-color: var(--mat-toggle-selected-background-color);
                    color: var(--mat-toggle-selected-color);
                }
            }

            &.mat-button-toggle-vertical {
                .mat-button-toggle {
                    padding: 0;

                    border-left: 1px solid var(--mat-toggle-border-color);
                    border-right: 1px solid var(--mat-toggle-border-color);

                    &-label-content {
                        padding: 8px 12px;
                    }

                    &:first-child {
                        border-top-left-radius: 8px;
                        border-top-right-radius: 8px;
                        border-bottom-left-radius: 0;
                        border-bottom-right-radius: 0;
                    }
        
                    &:last-child {
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;
                        border-bottom-left-radius: 8px;
                        border-bottom-right-radius: 8px;

                        border-bottom-width: 1px;
                    }
                }
            }
        }
        
    }
}

  