@import 'colors';

$track-color: $gray-30 !default;
$thumb-color: $gray-70 !default;
$thumb-border-color: $gray-70 !default;

$thumb-radius: 8px !default;
$thumb-height: 16px !default;
$thumb-width: 16px !default;
$thumb-shadow-size: 2px !default;
$thumb-shadow-blur: 2px !default;
$thumb-shadow-color: rgba($gray-0, .2) !default;
$thumb-border-width: 1px !default;

$track-width: 100% !default;
$track-height: 8px !default;
$track-shadow-size: 1px !default;
$track-shadow-blur: 1px !default;
$track-shadow-color: transparent !default;
$track-border-width: 1px !default;
$track-border-color: $gray-15 !default;

$track-radius: 4px !default;
$contrast: 0% !default;

$ie-bottom-track-color: darken($track-color, $contrast) !default;

@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
  box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color, 0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track {
  cursor: default;
  height: $track-height;
  transition: all .2s ease;
  width: $track-width;
}

@mixin thumb($thumb-color: $thumb-color, $adjustment: 0) {
  // @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);
  background: $thumb-color;
  // border: $thumb-border-width solid $thumb-border-color;
  border-radius: $thumb-radius;
  cursor: default;
  height: $thumb-height + $adjustment;
  width: $thumb-width + $adjustment;
}

@mixin disabled {
  cursor: not-allowed;
}

[type='range'].light { 
    $track-border-color: $gray-70;
    $track-color: $gray-70;
    $thumb-color: $gray-50;

    &:focus {
      &::-webkit-slider-runnable-track {
        background: lighten($track-color, $contrast);
      }

      &::-ms-fill-lower {
        border: $track-border-width solid $track-border-color;
        background: $track-color;
      }

      &::-ms-fill-upper {
        border: $track-border-width solid $track-border-color;
        background: lighten($track-color, $contrast);
      }
    }
    
    &::-webkit-slider-runnable-track {
      border: $track-border-width solid $track-border-color;
      background: $track-color;
    }

    &::-webkit-slider-thumb {
      @include thumb($thumb-color);
    }

    &::-moz-range-track {
      border: $track-border-width solid $track-border-color;
      background: $track-color;
    }

    &::-moz-range-thumb {
      @include thumb($thumb-color, -4);
    }

    &::-ms-thumb {
      @include thumb($thumb-color, -4);
    }

}

[type='range'],
[type='range'].dark {
    &:focus {
      &::-webkit-slider-runnable-track {
        background: lighten($track-color, $contrast);
      }

      &::-ms-fill-lower {
        border: $track-border-width solid $track-border-color;
        background: $track-color;
      }

      &::-ms-fill-upper {
        border: $track-border-width solid $track-border-color;
        background: lighten($track-color, $contrast);
      }
    }

    &::-webkit-slider-runnable-track {
      border: $track-border-width solid $track-border-color;
      background: $track-color;
    }

    &::-webkit-slider-thumb {
      @include thumb($thumb-color);
    }

    &::-moz-range-track {
      border: $track-border-width solid $track-border-color;
      background: $track-color;
    }

    &::-moz-range-thumb {
      @include thumb($thumb-color, -4);
    }

    &::-ms-thumb {
      @include thumb($thumb-color, -4);
    }
}

[type='range'] {
  -webkit-appearance: none;
  background: transparent;
  margin: $thumb-height / 2 0;
  width: $track-width;

  &::-moz-focus-outer {
    border: 0;
  }

  &:focus {
    outline: 0;
  }

  &::-webkit-slider-runnable-track {
    @include track;
    @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
    border-radius: $track-radius;
  }

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    margin-top: ((-$track-border-width * 2 + $track-height) / 2 - $thumb-height / 2);
  }

  &::-moz-range-track {
    @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
    @include track;
    border-radius: $track-radius;
    height: $track-height / 2;
  }

  &::-ms-track {
    @include track;
    background: transparent;
    border-color: transparent;
    border-width: ($thumb-height / 2) 0;
    color: transparent;
  }

  &::-ms-fill-lower {
    @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
    background: $ie-bottom-track-color;
    border-radius: ($track-radius * 2);
  }

  &::-ms-fill-upper {
    @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
    background: $track-color;
    border-radius: ($track-radius * 2);
  }

  &::-ms-thumb {
    margin-top: $track-height / 4;
  }

  &:disabled {
    &::-webkit-slider-thumb {
      @include disabled;
    }

    &::-moz-range-thumb {
      @include disabled;
    }

    &::-ms-thumb {
      @include disabled;
    }

    &::-webkit-slider-runnable-track {
      @include disabled;
    }

    &::-ms-fill-lower {
      @include disabled;
    }

    &::-ms-fill-upper {
      @include disabled;
    }
  }
}